	module.exports = {"data":{"markdownRemark":{"html":"<p>给R写了一封公函，推荐他去比利时，教委下达了共同体提供的作博士后研究的名额，系里准备让他去竞争。青年教师的培养要有一个战略，现在主要是培养他们的世界意识和学术修养，由此才能有世界性的学者，至少对世界和中国有比较好的把握。</p>\n<p>和辩论队的所有队员一起到上钢三厂去。上钢三厂这个地方已经不认识了。过去经常到那里去，那是文化大革命期间，都是农田，我们认为到那里去就像到乡下去一样。现在发展得很快。</p>\n<p>参观了现代化的车间之后，总有一种想法，就是一个社会的政治体制和行政体制能否最后建立得像一个现代化的技术系统那样，完全在电脑的控制之下，非常有秩序和程序，能够有效地运转和产出。大概在社会系统中要实现这一点是困难的，因为社会的因素变异性很大，人文因素的变化更大，很难根据确定的程序来管理，而要有更多应变能力。但是，总觉得是可以努力的，如何来建立这样的结构和系统，一要看社会发展的成熟程度，二要看对系统的研究本身在理论上达到了什么样的程度。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0053.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0053.md","frontmatter":{"title":"3月9日 星期三"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0053.md absPath of file >>> MarkdownRemark"}}

